import { Loading } from 'element-ui';

/**
 * loading
 * @param { startLoading } 
 * @param { endLoading } 
 */
export default {
  load: {},
   /**
     * loading
     * @param lock  load(true)
     * @param text  title
     * @param background  lodaing
     * @param target  lodaing
     */
  startLoading: (loadParam = {
    lock: true,
    text: '',
    background: 'rgba(255, 255, 255, 0.8)',
    target: document.querySelector('.loadingtext')
  }) => {
    this.load = Loading.service(loadParam);
  },
   /**
     * loading
     * @param time  loading
     *
     */
  endLoading: (time) => {
    (!isNaN(time))?(typeof time==='string')?time=parseInt(time):time:(time=100);
    time ? setTimeout(() => {
      this.load.close();
    }, time) : this.load.close();
    
  }
}



