                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <!--  -->
            <el-row type="flex">
              <el-col :span="18">
                <el-form-item label="" class="authorized-space">
                  <i style="position: absolute; z-index: 10; right: 10px;" class="iconfont icon-member"
                     @click="openDailog('originator_dailog', 'beAuth')"></i>
                  <el-select
                    v-model="userForm.authorizedUser[0]"
                    filterable
                    clearable
                    remote
                    reserve-keyword
                    popper-class="orgstates"
                    placeholder=""
                    :remote-method="querySearchUser"
                    value-key='userId'
                    @change="personChange"
                    @visible-change="visibleChange"
                    :loading="loadingPerson"
                    ref="userId"
                    @click.native="burialPointEvent('--')">
                    <el-option
                      v-for="(item,index) in orgStates"
                      :key="index"
                      :label="item.value"
                      :value="item">
                      <div class="userheight">{{item.value}}({{item.userId}})</div>
                      <div v-if="item.companyName" class="fullpathsize">{{ item.companyName }} {{item.orgFullPath}}</div>
                      <div v-else class="fullpathsize">{{item.orgFullPath}}</div>
                    </el-option>
                    <el-button :loading="originatorLoading" class="load-more" v-show="orgStates.length >= 10 && orgStates.length < originatorTotal" @click="loadMore(searchValParam)">
                      
                      <i class="el-icon-caret-bottom" style="color: #119cff;"></i>
                    </el-button>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <!--  -->
            <el-row>
              <el-col :span="18">
                <el-form-item label="" class="authorized-space">
                  <el-select v-model="userForm.recType"
                             @change="changeRecType"
                             placeholder=""
                             @click.native="burialPointEvent('--')">
                    <el-option
                      v-for="item in recTypeList"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <!--  -->
            <el-row justify="start">
              <el-col :span="18">
                <el-form-item label="">
                  <div class="money-box">
                    <el-form-item style="float: left;">
                      <el-date-picker
                        class="todoDate"
                        v-model="userForm.date[0]"
                        value-format="yyyy-MM-dd"
                        type="date"
                        :picker-options="pickerOptionsBefore"
                        @focus="hideDate"
                        :clearable="false"
                        placeholder="">
                      </el-date-picker>
                    </el-form-item>
                    <el-form-item style="float: left;">~</el-form-item>
                    <el-form-item style="float: right;">
                      <el-date-picker
                        class="todoDate"
                        style="float: right;"
                        v-model="userForm.date[1]"
                        value-format="yyyy-MM-dd"
                        type="date"
                        :picker-options="pickerOptionsAfter"
                        @focus="hideDate"
                        :clearable="false"
                        placeholder="">
                      </el-date-picker>
                    </el-form-item>
                  </div>
                </el-form-item>

              </el-col>
            </el-row>
            <!--  -->
            <el-row type="flex" justify="start">
              <el-col :span="18">
                <el-form-item label="" class="mainLeft-operatorer">
                  <span>{{userForm.operator}}</span>
                </el-form-item>
              </el-col>
            </el-row>
            <!--  -->
            <el-row type="flex" justify="start">
              <el-col :span="18">
                <el-form-item label="" class="mainLeft-operatordate">
                  <span>{{userForm.operationDate}}</span>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </el-aside>
        <!--  end -->

        <!--  begin -->
        <el-container class="authorized-mainRight">
          <!--  -->
          <el-header class="processHeader" ref="rightHeader" style="height: auto !important; border-bottom: 0;">
            <div class="hotProcess">
              <div class="hotProcess-title">
                <el-row>
                  <el-col :span="9">
                    <span class="img-title"></span>
                  </el-col>
                  <el-col :span="14" style="margin-top:-11px; float: right;">
                    <!--  -->
                    <div @click="showToggle" style="cursor: pointer; float: right;">
                      <span style="font-size: 14px;" v-text="arrt"></span>
                      <i :class="icon" style="color: #1fa0fb;font-weight: bolder;font-size: 18px"></i>
                    </div>
                    <!--  -->
                    <span style="margin-right: 15px; float: right;">
                      <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll"
                                   @change="handleCheckAllChange"></el-checkbox>
                    </span>
                  </el-col>
                </el-row>
              </div>
              <div>
                <div v-show="showList" class="hotProcess-container">
                  <div ref="ele">
                    <el-checkbox-group v-model="chekedIDs" size="small" @change="handleCheckedProcessChange">
                      <el-checkbox :title="item.processName" v-for="(item, index) in processList" :label="item"
                                   :key="index">{{item.processName}}
                      </el-checkbox>
                    </el-checkbox-group>
                    <div @click="getMore" v-if='!loading&&hasData'>
                      <el-button type="text"></el-button>
                    </div>
                    <div style="text-align: center; margin: 15px 0;" v-if='!hasData'>
                      <span></span>
                    </div>
                  </div>
                  <div class="loading-box" v-if='loading'>
                    <i class="el-icon-loading"></i>
                  </div>
                </div>
              </div>
            </div>
          </el-header>
          <!--  -->
          <el-main class="loadingtext" style="height:100%; padding: 0; margin: 0;">
            <div class="compProcess">
              <div style="height: 100%;">
                <div class="compProcess-select">
                  <span></span>
                  <!---->
                  <el-select style="width: 300px; margin-left: 15px;" ref='selectTree' v-model="organizeName"
                             placeholder="" @click.native="burialPointEvent('--')">
                    <el-option class="organize-list-box" style="height: auto;padding:0 !important;" value="" :disabled=true>
                      <div style="height: 100%; width: 100%;">
                        <el-tree
                          :data="organizeList"
                          :highlight-current="true"
                          node-key='orgID'
                          default-expand-all
                          :expand-on-click-node='false'
                          empty-text=''
                          :props="propsOrg"
                          @node-click="selectTreeClick"
                          ref="orgtree"></el-tree>
                      </div>
                    </el-option>
                  </el-select>
                  <span style="margin-right: 30px; float: right;">
                    <el-checkbox v-model="processCheckAll" :indeterminate="indeterminateProcess" @change="setCheckedAllNodes"></el-checkbox>
                  </span>
                </div>
                <!--  -->
                <div class="mainRight-tree" :style="showList ? { 'height': bodyHeight - headHeight - 150+ 'px' } : { 'height': bodyHeight - 200 + 'px' }">
                  <el-tree
                    :highlight-current="true"
                    @check-change="handleCheckChanges"
                    :data="thirdData"
                    ref="tree"
                    node-key="id"
                    :default-checked-keys="checkedKeys"
                    :default-expanded-keys="categoryID"
                    show-checkbox
                    :props="props"
                    @node-expand="handleNodeExpand"
                    @node-click="handleNodeClick"
                    @check="burialPointEvent('--[]')">
                  </el-tree>
                </div>
              </div>
            </div>
          </el-main>
        </el-container>
        <!--  end -->
      </el-container>
    </el-container>
    <!--  -->
    <component :is="comp.component" v-if="showDialog" @hideComponent="hideComponent"
               v-on:sureSubmit="showMsgfromchild" v-bind:childDataUrl="childDataUrl" v-bind:queryName="queryName"
               v-bind:oneCheck="oneCheck"></component>
  </div>
</template>


<script>
  import util from "../../js/util";
  import orginatordialog from "../dailog/originator_dailog.vue";
  import moment from "moment";
  import loading from '../../plugins/loading/loading';
  // `debounce`  Lodash 
  import {debounce} from "lodash";
  import { mapGetters } from 'vuex'
  export default {
    name: "authorizedStyle",
    components: {
      originator_dailog: orginatordialog,
    },
    computed: {
      // 
      bodyHeight() {
        return document.documentElement.clientHeight;
      },
      ...mapGetters(['bodyHeight'])
    },
    //
    created(param){
      let that = this;
      // `debounce`  Lodash 
      that.debouncedSearchUser = debounce(that.getUsers, 0,param);
      that.debouncedauthorizedUser = debounce(that.getUsers, 0,param);
    },
    //
    mounted() {
      let that = this;
      // \ 
      Promise.all([that.getOftenProcess(), that.getOrganizeList()]).then(result => {
        if(result[1].state){
          let node = this.$refs.orgtree.getNode(result[1].orgID);
          that.organize.orgID = result[1].orgID;
          if(node){
            this.$refs.orgtree.setCurrentKey(result[1].orgID);
            this.setOrganize(node);
            this.getChildenList(result[1].orgID, result[1].orgName);
          }
        }
        if (this.params) {
          //
          this.initEditForm()
        } else {
          //
          this.initAddForm()
        }
      }).catch(error => {
        that.$message.error(error)
      })
    },
    data() {
      return {
        originatorLoading: false,
        searchValParam: {}, // 
        commonProcessIDs: [], // id
        checkedNodeProcessIDs: [], //  id
        categoryID: [], // 
        headHeight: '',
        //
        params: this.$route.query.params,
        // 
        pageIndex: 1,
        //  
        pageSize: 12,
        // 12
        processList: [],
        //
        userForm:{
          //bind   
          authorUser:[],
          //bind   
          authorizedUser:[],
          //bind   1
          recType: 1,
          //    7
          date:[moment(new Date()).format("YYYY-MM-DD"), moment().add("days", 7).format("YYYY-MM-DD")],
          //  
          operator:util.getLocalStorage("username"),
          //
          operationDate:moment(new Date()).format("YYYY-MM-DD"),
          // GUID
          lotNumber:''
        },
        // 
        recTypeList:[{value: 1,label: ""}, {value: 2, label: ""}],
        form: {},
        //  
        fromAuthorizingPerson: [],
        //  
        toAuthorizingPerson: [],
        // loading
        loadingPerson: false,
        // 
        authType: '',
        //
        queryName: "",
        //  
        orgStates: [],
        //   
        orgPeopleStates: [],
        // id
        chekedIDs: [],
        //tree   key
        checkedKeys:[],
        // Id
        processIDs:[],

        checkAll: false, //  
        processCheckAll: false,//  
        processTreeData: [], //  toTree
        showList: true, //
        isIndeterminate: false, // CheckBox
        indeterminateProcess: false, // CheckBox
        loading: false, // loading
        oneCheck: true,// 
        hasData: true,// 
        childDataUrl: {url: "", selectedPerson: []}, //
        showDialog: false, //
        comp: {},
        fromUserList: [], //
        selectedFromUser: {secretaryID: '', managerIDList: ''},//
        processArrays: [],//
        thirdData: [],
        props: {
          id: "id",
          label: "name",
          children: "Child"
        },
        checked: false,
        fullHeight: document.documentElement.clientHeight,
        arrt: "",
        icon: "el-icon-arrow-up",
        isShow: true,
        isWorkProcess: [], // 
        processData: [], // 
        minDate: '', // 
        // 
        pickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.userForm.date[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        pickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.userForm.date[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },
        // tree
        propsOrg: {
          label: 'orgName',
          value: 'orgID',
          children: 'm_ChildOrg'
        },
        organizeName: '',//
        activeOrganizeName: [],//
        organizeList: [],//
        //
        organize:{orgID:''},
        processCategoryIDs: [], // id
        originatorPageIndex: 0,
        originatorPageSize: 10,
        originatorTotal: 0,
        isLoad: true
      };
    },
    methods: {
      /**
       * 
       */
      burialPointEvent(event){
        util.burialPoint(event);
      },
      // 
      loadMore(param) {
        this.originatorPageIndex ++;
        this.getUsers(param, true);
      },
      // input  icon
      hideDate(val) {
        util.burialPoint('--');
        setTimeout( () => {
          let dateNodeStart = undefined;
          let dateNodeEnd = undefined;
          let startBool = null;
          let endBool = null;
          if (document.getElementsByClassName('el-picker-panel')[0]) {
            startBool = true;
          }
          if (document.getElementsByClassName('el-picker-panel')[1]) {
            endBool = true;
          }

          dateNodeStart = startBool ? document.getElementsByClassName('el-picker-panel')[0] : '';
          dateNodeEnd = endBool ? document.getElementsByClassName('el-picker-panel')[1] : '';

          if (val.$el.firstElementChild == document.activeElement) {
            startBool ? dateNodeStart.style.display = 'none' : '';
            endBool ? dateNodeEnd.style.display = 'none' : ''
          } else {
            startBool ? dateNodeStart.style.display = 'block' : '';
            endBool ? dateNodeEnd.style.display = 'block' : ''
          }
        }, 10)
      },
      /**
       *   
       * @returns {Promise}
       */
      getOftenProcess() {
        let that = this;
        return new Promise((resolve, reject) => {
          let url =`/api/oftenApproved?pageIndex=${that.pageIndex - 1}&pageSize=${that.pageSize}`;
          that.$axios.post(url, {}).then(result => {
            that.processList = result.data.result;
            //    /
            if (result.data.result.length <= 12) {
              this.hasData = false;
            }
            that.setChekedOften();
            setTimeout( () => {
              this.headHeight = this.$refs.rightHeader.$el.clientHeight;
            }, 1000);
            resolve(true)
          }).catch(err => {
            reject(`!ERROR MESSAGE:${err}`);
          });
        })
      },
      /**
       * 
       * @returns {Promise}
       */
      getOrganizeList() {
        let that = this;
        return new Promise((resolve, reject) => {
          that.$axios.post("/api/companytree/page", {}).then(res => {
            if (res.data.companyTreeList.length > 0) {
              that.organizeList = res.data.companyTreeList;
              let orgID = that.organizeList[0].orgID;
              let orgName = that.organizeList[0].orgName;
              resolve({state: true, orgID, orgName});
            } else {
              resolve({state: false, orgID, orgName});
            }
          }).catch(err => {
            reject(`!ERROR MESSAGE:${err}`)
          })
        })
      },
      /**
       * 
       */
      getChildenList(orgId, orgName) {
        this.categoryID = [orgId];
        this.isWorkProcess = [];
        this.processCheckAll = JSON.stringify(this.unique(this.processArrays, 'id')).indexOf(orgId) > -1;
        loading.startLoading();
        let obj = {
          orgId: orgId,  // id
          isUse: "1", // 
          name: "",   // 
          isHide: "1", // 
          isCategory: "",//  1  0
          isDailog: "0"
        };
        let url = "/api/processCategory";
        this.$axios.post(url, obj).then(res => {
          if (res.status === 200) {
            let childData = this.toTree(res.data.result);
            // for(let item of childData){
            //   this.$set(item, 'Child', {
            //     "id": orgId + 'test',
            //     "name": 'test'
            //   });
            // }
            let data = [];
            data.push({
              'id': orgId,
              "Child": childData,
              "name": orgName,
              'disabled' : false,
              'isUpgrade': '0'
            });
            this.thirdData = data;
            if (this.thirdData.length == null) {
              this.$message.success("");
            }
          } else {
            this.$message.error("");
          }
          loading.endLoading();
        }).catch(err => {
          this.$message.error(`child${err}`);
          loading.endLoading();
        });
      },
      // +
      handleNodeExpand(data,node) {
        if (data.isUpgrade === '0') { return; } // 
        if (data.processId !== null) { return; } // 
        // isLoad = true   Child 
        if(!data.Child || data.Child.length == 0){this.isLoad = true;}
        // isProcess    isLoad = false
        if (!data.isProcess) {
          this.firstLoad(data,node);
          this.isLoad = false;
        }
      },
      //  
      handleNodeClick(data,node) {
        if (data.isUpgrade === '0') { return; } // 
        if (data.processId !== null) { return; } // 
        // isLoad = true   Child 
        if(!data.Child || data.Child.length == 0){this.isLoad = true;}
        // isProcess   ++ isLoad = false 
        if (this.isLoad && !data.isProcess) {
          this.firstLoad(data,node);
        }
      },
      firstLoad(data,node) {
        // 
        for (let i = 0; i < this.processArrays.length; i++) {
          this.checkedKeys.push(this.processArrays[i].id);
        }
        for (let i = 0; i < this.checkedKeys.length; i++) {
          if(!node.checked) {
            this.checkedKeys.splice(i, 1);
            i--;
          }
        }
        this.checkedKeys = Array.from(new Set(this.checkedKeys));
        // 
        if(!data.isProcess){
          this.categoryID = [];
          this.categoryID.push(data.id);
          this.getProcessTree(data,node);
        }
      },
      getProcessTree(data,node) {
        loading.startLoading();
        this.isWorkProcess = [];// 
        let obj = {
          id: data.id,  // id
          orgId: this.organize.orgID,
          isUse: "1", // 
          name: "",   // 
          isHide: "1", // 
          isCategory: "0",//  1  0
          isDailog: "0"
        };
        let url = "/api/processCategoryTree";
        this.$axios.post(url, obj).then(res => {
          if (res.status === 200) {
            data.isProcess = true;
            let dataSet = res.data.result;
            this.processData = dataSet;
            for (let it of dataSet) {
              if (it && it.hasOwnProperty('processId') && it.processId !== null && it.processId !== '0' && it.isUpgrade !== '0') {
                this.processTreeData.push(it);
              }
            }
            dataSet = this.deleteNode(dataSet);
            for (let it of dataSet) {
              if (it && it.hasOwnProperty('processId') && it.processId !== null && it.processId !== '0') {
                this.isWorkProcess.push(it);
                //  processList
                this.$refs.tree.append(it, node);
              }
            }
            // 
            if(this.params){
              this.checkedIDs();
            } else {
              //  
              let IDs = [];
              for (let i = 0; i < this.processArrays.length; i++) {
                this.checkedKeys.push(this.processArrays[i].id);
                if (this.checkedKeys.indexOf(this.processArrays[i].id) > -1) {
                  IDs.push(this.processArrays[i].id)
                }
              }
              this.checkedKeys = IDs;
            }
            //  
            if(node.checked) {
              let childProcessIDs = [];
              for (let item of data.Child) {
                childProcessIDs.push(item.id)
              }
              setTimeout( () => {
                this.checkedKeys = childProcessIDs
              }, 10)
            }

          } else {
            this.$message.error("");
          }
          loading.endLoading();
        }).catch(err => {
          this.$message.error(`child${err}`);
          loading.endLoading();
        });
      },
      checkedIDs(){
        let checkedIDs = [];
        let that = this;
        for (let j in that.processIDs) {
          checkedIDs.push(this.processIDs[j]+'');
        }
        let processIds = this.getProcessId(that.processList,'processid')
        let count = checkedIDs.length;
        for (let j=0;j<count;j++) {
          if (processIds.indexOf(checkedIDs[j])>=0) {
            checkedIDs.splice(j, 1);
            j--
          }
        }
        this.checkedKeys = checkedIDs;
      },
      getProcessId(list,key){
        let data = []
        for(let it of list){
          if(it[key]){ data.push(it[key]+'')}
        }
        return data
      },
      /**
       *  
       */
      initAddForm(){
        // 
        let userId = util.getLocalStorage("loginname")
        let value = util.getLocalStorage("username")||util.getLocalStorage("loginname")
        let vankeId = `vanke\\${userId}`
        let user = {userId,value,vankeId}
        this.userForm.authorUser[0] = value
        this.fromAuthorizingPerson[0] = user
        this.userForm.operator = value
      },
      /**
       *  
       */
      initEditForm(){
        let that = this
        if (!that.params.lotNumber) {
          return;
        }
        that.$axios.post("/api/getUserAuthorityToEdit", {lotNumber: this.params.lotNumber}).then(res => {
          if(res.data.userAuthorityList.length >0){
            let checkedIDs = [];
            for (let item of this.processList) {
              checkedIDs.push(Number(item.processid));
            }
            for (let item of res.data.userAuthorityList[0].processIDs) {
              if (checkedIDs.indexOf(item) === -1) {
                this.processArrays.push({
                  id: item
                })
              }
            }
            that.setEditFrom(res.data.userAuthorityList[0])
          }
        });
      },
      /**
       *  
       */
      setEditFrom(data){
        this.userForm = {
          //bind   
          authorUser:[data.fromUserName],
          //bind   
          authorizedUser:[data.toUserName],
          //bind   1
          recType: data.recType,
          //    7
          date:[moment(data.beginTime).format("YYYY-MM-DD"), moment(data.endTime).format("YYYY-MM-DD")],
          //  
          operator:util.getLocalStorage("username"),
          //
          operationDate:moment(new Date()).format("YYYY-MM-DD"),
          // GUID
          lotNumber:data.lotNumber
        }
        //  
        let userId = data.fromUserID.replace('VANKE\\', '')
        let value = data.fromUserName
        let vankeId = data.fromUserID
        this.userForm.authorUser[0] = value
        this.fromAuthorizingPerson[0] = {userId,value,vankeId}
        //
        let touserId = data.toUserID.replace('VANKE\\', '')
        let tovalue = data.toUserName
        let tovankeId = data.toUserID
        this.userForm.authorizedUser[0] = data.toUserName
        this.toAuthorizingPerson[0] =  {'userId':touserId,'value':tovalue,'vankeId':tovankeId}

        //  Id
        this.processIDs = data.processIDs
        // -
        this.setChekedOften()
      },
      /**
       * -
       */
      setChekedOften(){
        let that = this;
        //  |
        if(that.params && that.processIDs){
          //    
          for (let it of that.processList) {
            if (that.processIDs.indexOf(Number(it.processid))>-1) {
              that.chekedIDs.push(it);
            }
          }
        }
        if (that.chekedIDs.length === that.processList.length && that.chekedIDs.length > 0) {
          that.checkAll = true;
        } else if (that.chekedIDs.length < that.processList.length && that.chekedIDs.length > 0) {
          that.isIndeterminate = true
        }
      },
      //
      selectTreeClick(data, node) {
        this.$refs.selectTree.visible = false;
        this.organize = data;
        this.setOrganize(node);
        // 
        this.getChildenList(data.orgID, data.orgName);
        // 
        let IDs = [];
        for (let i = 0; i < this.processCategoryIDs.length; i++) {
          this.checkedKeys.push(this.processCategoryIDs[i].id);
          if (this.checkedKeys.indexOf(this.processCategoryIDs[i].id) > -1) {
            IDs.push(this.processCategoryIDs[i].id)
          }
        }
        this.checkedKeys = IDs;
      },
      //   
      setOrganize(node) {
        this.activeOrganizeName = [];
        this.getPrarentNode(node); // 
        this.organizeName = this.activeOrganizeName.reverse().join('/');
      },
      //
      getPrarentNode(pNode) {
        if (pNode.data.orgName != undefined) {
          this.activeOrganizeName.push(pNode.data.orgName);
        }
        if (pNode.parent != undefined) {
          this.getPrarentNode(pNode.parent);
        }
      },
      // tree
      handleCheckChanges(data, checked, indeterminate) {
        if (!checked) {
          //  i
          for (let i = 0; i < this.processCategoryIDs.length; i++) {
            if (data.id === this.processCategoryIDs[i].id) {
              this.processCategoryIDs.splice(i, 1);
              i--;
            }
          }
          //  i
          for (let i = 0; i < this.processArrays.length; i++) {
            if (data.id === this.processArrays[i].id) {
              this.processArrays.splice(i, 1);
              i--;
            }
          }
        }
        this.getCheckedKeys(data, checked);
        // this.changeCheckedProcess(data, checked);
      },
      // changeCheckedProcess(val, bool){
      //   // 
      //   if (bool) {
      //     for (let item of this.processList) {
      //       if (val.processId == item.processid) {
      //         this.chekedIDs.push(item)
      //       }
      //     }
      //   }else {
      //     for (let item in this.chekedIDs) {
      //       if (val.processId == this.chekedIDs[item].processid) {
      //         this.chekedIDs.splice(item, 1);
      //       }
      //     }
      //   }
      // },
      //
      changeRecType(value) {
        if (value == '3') {
          this.userForm.date = [moment(new Date()).format("YYYY-MM-DD"), moment().add("days", 7).format("YYYY-MM-DD")];
        }
      },
      //
      getMore() {
        this.pageIndex = this.pageIndex + 1;
        this.getOftenProcess();
      },
      //---
      handleCheckAllChange(val) {
        util.burialPoint('--[]');
        if (val) {
          this.chekedIDs = this.processList;
          // let checkedKeys = [];
          // for (let item of this.chekedIDs) {
          //   checkedKeys.push(Number(item.processid));
          // }
          // this.$refs.tree.setCheckedKeys(checkedKeys);
        } else {
          this.chekedIDs = [];
          // this.$refs.tree.setCheckedKeys([]);
        }
        this.isIndeterminate = false;
      },
      //   
      handleCheckedProcessChange(value) {
        util.burialPoint('--[]');
        // let checkedKeys = [];
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.processList.length;
        this.isIndeterminate =
          checkedCount > 0 && checkedCount < this.processList.length;
        // for (let item of value) {
        //   checkedKeys.push(Number(item.processid));
        // }
        // this.$refs.tree.setCheckedKeys(checkedKeys);
      },
      // 
      showToggle() {
        this.showList = !this.showList;
        this.isShow = !this.isShow;
        if (this.isShow) {
          this.arrt = "";
          this.icon = "el-icon-arrow-up";
        } else {
          this.arrt = "";
          this.icon = "el-icon-arrow-down";
        }
      },
      // 
      openDailog(type, userType) {
        // 
        if (type == 'originator_dailog') {//
          this.oneCheck = true;
          this.authType = userType;
          //
          this.childDataUrl.getTree = "getTree";
          this.childDataUrl.companytree = "companytree/page";
          if (userType == 'auth') {
            util.burialPoint('--');
            //  
            this.childDataUrl.selectedPerson = this.fromAuthorizingPerson;
          } else if (userType == 'beAuth') {
            util.burialPoint('--');
            //   
            this.childDataUrl.selectedPerson = this.toAuthorizingPerson;
          }
        }
        this.comp.component = type;
        this.showDialog = true;
        util.showDailog_mask(); //ie
      },
      // 
      hideComponent() {
        this.showDialog = false;
        util.hiddenDailog_mask(); //ie 
      },
      // 
      showMsgfromchild(data) {
        let that = this;
        if (that.authType == 'auth') {// 
          if(data.peoArr && data.peoArr.length >0){
            let item = data.peoArr[0];
            this.userForm.authorUser = [item.name];
            this.fromAuthorizingPerson = [{
                userId: item.userId,
                value: item.name,
                vankeId:  item.vankeId
              }]
          }else{
            this.userForm.authorUser = [];
            this.fromAuthorizingPerson = []
          }
        } else if (that.authType == 'beAuth') { // 
          if(data.peoArr && data.peoArr.length >0){
            let item = data.peoArr[0]
            this.userForm.authorizedUser = [item.name]
            this.toAuthorizingPerson = [{
                userId: item.userId,
                value: item.name,
                vankeId:  item.vankeId
              }]
          }else{
            this.userForm.authorizedUser = []
            this.toAuthorizingPerson = []
          }
        }
        this.hideComponent();
      },
      // 
      getUsers(param, type) {
        let that = this;
        that.loadingPerson = !type;
        that.originatorLoading = true;
        that.$axios.post(`/api/getUser?pageIndex=${this.originatorPageIndex}&pageSize=${this.originatorPageSize}`, {name: param.userName, deptId: "", isWork: "1"}).then(result => {
          that.orgPeopleStates = [];
          that.orgStates = [];
          this.originatorTotal = result.data.pageControllerInfo.totalRecordCount;
            if(param.type == 'authorUser'){
              //
              for (let i = 0; i < result.data.result.length; i++) {
                that.orgPeopleStates.push({
                  value: result.data.result[i].displayName,
                  userId: result.data.result[i].adAccount,
                  companyName: result.data.result[i].companyName,
                  orgFullPath: result.data.result[i].orgNameFullPath,
                  'vankeId': result.data.result[i].vankeId
                });
              }
            }else{
              //
              for (let i = 0; i < result.data.result.length; i++) {
                that.orgStates.push({
                  value: result.data.result[i].displayName,
                  userId: result.data.result[i].adAccount,
                  companyName: result.data.result[i].companyName,
                  orgFullPath: result.data.result[i].orgNameFullPath,
                  'vankeId': result.data.result[i].vankeId
                });
              }
            }
            that.loadingPerson = false;
            that.originatorLoading = false;
          }).catch(err => {
             that.loadingPerson = false;
            that.originatorLoading = false;
            that.$message.error(`!ERROR MESSAGE:${err}`);
          });
      },
      //------------------------------------- begin
      //  
      querySearchUser(userName) {
        this.orgStates = [];
        var that = this;
        that.searchValParam.userName = userName;
        that.searchValParam.type = "authorizedUser";
        that.debouncedauthorizedUser({userName,type:"authorizedUser"});
      },
      // 
      personChange(data) {
        this.$set(this.$refs.userId, 'query', '');//
        if (data) {
          this.toAuthorizingPerson = [data]
        } else {
          this.toAuthorizingPerson = [];
        }
      },
      // 
      visibleChange(val) {
        //
        this.orgStates = [];
      },
      //------------------------------------- end
      //------------------------------------- begin
      /**
       *  
       */
      querySearchUserPeo(userName) {
        this.orgPeopleStates = [];
        let that = this;
        that.searchValParam.userName = userName;
        that.searchValParam.type = "authorUser";
        that.debouncedSearchUser({userName,type:"authorUser"});
      },
      /**
       * 
       */
      personChangePeo(data) {
        this.$set(this.$refs.userIdPeo, 'query', '');//
        if (data) {
          this.fromAuthorizingPerson = [data]
        } else {
          this.fromAuthorizingPerson = []
        }
      },
      /**
       *    
       */
      visibleChangePeo(val) {
        this.orgPeopleStates = [];
      },
      //------------------------------------- end
      // 
      setFormData(){
        let processIDs = [];
        let processNames = [];
        let processCategoryIDs = [];
        let checkedAll = '';
        let orgId = [];
        for (let it of this.processArrays) {
          processIDs.push(it.id); // ID
          processNames.push(it.name); // name
        }
        for (let it of this.processCategoryIDs) {
          processCategoryIDs.push(it.id); // ID
        }
        if (this.processCheckAll) {
          checkedAll = '0';
          for (let i = 0; i < this.processArrays.length; i++) {
            if(typeof this.processArrays[i].id === "string"){
              orgId.push(this.processArrays[i].id);
            }
          }
          processNames = [];
          processIDs = [];
          processCategoryIDs = [];
        }
        return {
          fromUserID: `VANKE\\${this.fromAuthorizingPerson[0].userId}`,    // Id
          fromUserName: this.fromAuthorizingPerson[0].value,   // 
          toUserIDs:  `VANKE\\${this.toAuthorizingPerson[0].userId}`,    // Id
          toUserNames: this.toAuthorizingPerson[0].value,   // 
          processNames: processNames,  //  
          processIDs: processIDs,    // ID 
          recType: this.userForm.recType,        //  1
          beginTime: `${this.userForm.date[0]} 00:00:00`,     // 
          endTime: `${this.userForm.date[1]} 23:59:59`,       // 
          lotNumber: "",     // GUID
          status: "0",       // 0-Active 1-Canceled 2-Expired
          userId: util.getLocalStorage("loginname"),        // Id
          operator: this.userForm.operator,      // 
          operationDate: this.userForm.operationDate, //
          currentTime: moment(new Date()).format("YYYY-MM-DD HH:mm:ss"),   //
          description: "",    //
          checkedAll: checkedAll,
          orgId: orgId.join(','),
          processCategoryIDs: processCategoryIDs.join(',')
        }
      },
      //
      btnSave() {
        util.burialPoint('--');
        let that = this;
        this.getOftenIds(); // id
        // this.getCheckedKeys('', ''); // 
        this.processArrays = this.unique(this.processArrays, 'id'); // 
        this.processCategoryIDs = this.unique(this.processCategoryIDs, 'id'); // 
        //
        //   v-model 
        if (!this.fromAuthorizingPerson || this.fromAuthorizingPerson.length ==0) {
          this.$message.warning("");
          return;
        }
        if (!this.toAuthorizingPerson || this.toAuthorizingPerson.length ==0) {
          this.$message.warning("");
          return;
        }
        if (this.processArrays.length === 0 && this.processCategoryIDs.length === 0) {
          this.$message.warning("");
          return;
        }
        if (!this.userForm.date || this.userForm.date.length < 1) {
          this.$message.warning("");
          return;
        }
        if (that.userForm.date && that.userForm.date.length > 1) {
          if(that.userForm.date[0] < '1753-01-01' || that.userForm.date[1] < '1753-01-01') {
            this.$message.warning('1753-01-01 ~ 9999-12-31');
            // this.userForm.date = [];
            return;
          }
        }
        let form = this.setFormData();
        //
        if (this.params) {
          form.lotNumber = this.params.lotNumber;
          // 
          this.editAuthor(form);
        } else {
          // 
          this.addAuthor(form);
        }
      },
      //
      addAuthor(form) {
        this.$axios.post("/api/UserAuthority", form).then(res => {
          if (res.data.result == 0) {
            this.$message.warning(res.data.messageList[0].message);
            return;
          } else if (res.data.messageList.length > 0) {
            this.$message.warning(res.data.messageList[0].message);
            return;
          } else {
            this.$message({
              type: "success",
              message: "!"
            });
            this.$router.push({path: "/authorized"});
          }
        }).catch(err => {
          this.$message.error(`!ERROR MESSAGE:${err}`);
        });
        this.processArrays = [];
      },
     //
      editAuthor(form) {
        this.$axios
          .post("/api/getUserAuthorityToUpdate", form)
          .then(res => {
            let result = res.data.result;
            if (result) {
              if (res.data.resultMessage != '') {
                this.$message.warning(res.data.messageList[0].message);
              } else {
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.$router.push({path: "/authorized"});
              }
            } else {
              let msg = res.data.messageList[0].message;
              this.$message.warning(msg);
            }
          })
          .catch(err => {
            this.$message.error(`!ERROR MESSAGE:${err}`);
          });
        this.processArrays = [];
      },
      //id
      getOftenIds() {
        this.commonProcessIDs = [];
        if (this.chekedIDs.length > 0) {
          for (let item of this.chekedIDs) {
            this.commonProcessIDs.push({'id': Number(item.processid), 'name': item.processName});
          }
        }
        this.processArrays = this.processArrays.concat(this.commonProcessIDs);
      },
      //
      btnCancle() {
         this.$router.push({path: "/authorized"});
       },
      // 
      removeProcessIDs(data, checked) {
        if (checked && data.Child && data.processId === null) {
          let processIDs = [];
          for (let item of data.Child) {
            processIDs.push(item.id)
          }
          for (let i = 0; i < this.processArrays.length; i++) {
            if (processIDs.indexOf(this.processArrays[i].id) > -1) {
              this.processArrays.splice(i, 1);
              i--;
            }
          }
        }
      },
      //
      getCheckedKeys(data, checked) {
        let list = this.$refs.tree.getCheckedNodes();
        // 
        for (let i = 0; i < list.length; i++) {
          if (list.length > 0 && list[i].processId === null) {
            this.processCategoryIDs.push({
              "id": list[i].id,
              "name": list[i].name
            })
          }
        }
        // 
        for (let i = 0; i < list.length; i++) {
          if (list.length > 0 && list[i].processId !== null) {
            this.processArrays.push({
              "id": list[i].id,
              "name": list[i].name
            })
          }
        }
        // 
        this.processArrays = this.processArrays.concat(this.commonProcessIDs);
        // 
        let keys = this.$refs.tree.getCheckedKeys();
        if (keys[0] === this.organize.orgID) {
          this.processCheckAll = true;
          this.indeterminateProcess = false;
        } else if (keys.length === 0) {
          this.processCheckAll = false;
          this.indeterminateProcess = false;
        } else {
          this.processCheckAll = false;
          this.indeterminateProcess = true;
        }
        // 
        setTimeout( () => {
          this.removeProcessIDs(data, checked);
        }, 100)
      },
      // 
      setCheckedAllNodes() {
        util.burialPoint('--[]');
        this.processData = [];
        if (this.processCheckAll) {
          // for (let it of this.processTreeData) {
          //   if (it && it.hasOwnProperty('processId') && it.processId !== null && it.processId !== '0') {
          //     this.processData.push(it);
          //   }
          // }
          this.$refs.tree.setCheckedKeys([this.organize.orgID]);
        } else {
          this.$refs.tree.setCheckedKeys([]);
        }
      },
      toTree(data) {
        if (data == null || data.length == 0) return [];
        data.forEach(function (item) {
          delete item.Child;
        });

        var map = {};
        data.forEach(function (item) {
          map[item.id] = item;
        });

        var val = [];
        data.forEach(function (item) {
          var parent = map[item.pid];
          if (parent) {
            (parent.Child || (parent.Child = [])).push(item);
          } else {
            val.push(item);
          }
        });
        return val;
      },
      // 
     /* disabledNode(data) {
        let nodeData = [];
        for (let it of data) {
          if (it) {
            if (it.hasOwnProperty("processId") && (it.processId == null || it.processId == '0')) {
              it.disabled = true;
            }
            nodeData.push(it);
          }
        }
        return nodeData;
      },*/
      //isUpgrade=0
      deleteNode(data) {
        for (let i in data) {
          if (data[i].isUpgrade == "0") {
            delete data[i];
          }
        }
        return data;
      },
      //
      unique(list, key) {
        var arr = [];
        for (var i = 0; i < list.length; i++) {
          if (i == 0) {
            arr.push(list[i]);
          }
          let b = false;
          if (arr.length > 0 && i > 0) {
            for (var j = 0; j < arr.length; j++) {
              if (arr[j][key] == list[i][key]) {
                b = true;
              }
            }
            if (!b) {
              arr.push(list[i]);
            }
          }
        }
        return arr;
      }
    }
  };
</script>

