          color: #000;
        }
        .el-breadcrumb {
          position: relative;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
    .authorized-mode {
      position: absolute;
      top: 107px;
      .authorized-mode-padding {
        padding: 0 50px;
      }
      .authorized-mode-select {
        float: left;
      }
    }

    .el-aside {
      // background-color: #D3DCE6;
      // color: #333;
      // text-align: center;
      line-height: 40px;
      margin-left: 10px;
      border-radius: 10px;
      min-width: 600px;
    }
    .authorized-mainLeft {
      width: 50%;
      background-color: #ffffff;
      padding: 40px 50px;

      & .el-textarea {
        height: 176px;
        resize: none;
      }
      & .mainLeft-operatorer {
        margin-bottom: 0;
        /*padding-top: 30px;*/
      }
      & .el-form-item span {
        position: absolute;
        left: 0px;
      }

      & .authorized-space {
        text-align: left;
        .userheight {
          height: 25px !important;
          font-weight: 600;
        }
      }
    }

    .authorized-mainLeft-padding {
      padding: 0;
    }

    .authorized-mainRight {
      border-left: 1px solid #F1F5F8;
      width: 50%;

      .mainRight-tree {
        padding: 0 20px;
        margin-top: 10px;
        overflow: auto;
      }
    }

    .el-main {
      background-color: #ffffff;
      color: #333;
      margin: 0 10px;
      border-radius: 10px;
      overflow: hidden;
      .compProcess {
        height: 100%;
        background: #ffffff;
        border-radius: 8px;
        .compProcess-title {
          text-align: left;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          padding-left: 5px;
        }
        .compProcess-select {
          background: #F8F9FB;
          text-align: left;
          height: 40px;
          line-height: 40px;
          font-size: 14px;
          padding-left: 30px;
          position: relative;
        }
        .compProcess-select:before {
          content: '';
          width: 4px;
          height: 15px;
          display: inline-block;
          background: #1FA0FC;
          position: absolute;
          left: 20px;
          top: 50%;
          -webkit-transform: translateY(-50%);
          -moz-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
          -o-transform: translateY(-50%);
          transform: translateY(-50%);
        }
      }
    }

    @media screen and (max-width: 1224px) {
      .authorized .hotProcess-container .el-checkbox-group .el-checkbox__label {
        width: 138px;
        border: 1px solid red;
      }
      .authorized .hotProcess-container :nth-child(2n).el-checkbox {
        margin-left: 49px;
      }
      .authorized .hotProcess-container :nth-child(2).el-checkbox {
        margin-left: 49px !important;
      }
      .authorized .el-header .hotProcess .hotProcess-container {
        width: 484px !important;
      }
    }
  }
</style>

<style lang="scss">
  @media screen and (min-width: 1201px) and (max-width: 1400px){
    .authorized .el-aside {
      min-width: 450px !important;
    }
  }
  @media screen and (max-width: 1200px){
    .authorized .el-aside {
      min-width: 400px !important;
    }
  }
  /*     */
  .authorized {
    // 
    .money-box .el-form-item{
      float: left;
      margin-bottom: 0;
      margin-right: 0;
      width: 42%;
      .el-date-editor.todoDate.el-input.el-input--prefix.el-input--suffix.el-date-editor--date {
        width: 100% !important;
        .el-input__inner {
          padding: 0 !important;
          text-align: center;
        }
      }
      .el-form-item__content {
        .el-date-editor {
          .el-input__prefix {
            left: inherit;
            right: 0;
          }
        }
      }
    }

    //  ~
    .money-box .el-form-item:nth-child(2){
      width:6%;
      margin: 0 10px;
      .el-form-item__content{
        text-align: center;
      }
    }
    .authorized-mainLeft {
      .icon-cancel, .icon-preservation, .icon-export, .icon-screening {
        color: #fff !important;
      }
    }
    .authorized-mainRight {
      .el-tree-node__content:hover, .el-tree--highlight-current .el-tree-node.is-current > .el-tree-node__content:hover {
        color: #606266;
        background: none;
      }
      /*  */
      .el-tree-node__content {
        color: #606266;
        background: none;
      }
      .el-tree-node__content:hover {
        background: #f5f7fa !important;
        color: #606266 !important;
      }
      .el-tree-node__content:visited {
        background: #f5f7fa !important;
        color: #606266 !important;
      }
      .el-tree-node__content:active {
        background: #f5f7fa !important;
        color: #606266 !important;
      }
    }
    .authorized-mainLeft {
      .el-date-editor.el-range-editor.el-input__inner.el-date-editor--daterange {
        .el-input__icon.el-range__icon.el-icon-date {
          position: absolute;
          right: 0;
          top: 0;
        }
      }
      .el-select__caret.el-input__icon.is-show-close {
        margin-right: 20px;
      }
    }
  }
  .el-scrollbar__view.el-select-dropdown__list {
    .el-tree-node__content:hover {
      color: #ffffff !important;
      background: #1fa0fd !important;
    }
  }
</style>



