// export const util = {
import zhuge from './zhuge_SDK'
  export default {
    version:'Version: 1.0.0',
  /**
   * 
   * @param timeZone
   * @param old
   * @returns {Date}
   */
  getTimeByTimeZone(timeZone, old) {
    var d = new Date(old),
      localTime = d.getTime(),
      localOffset = d.getTimezoneOffset() * 60000, //,
      utc = localTime + localOffset, //utcGMT
      offset = timeZone, //+8   -5
      localSecondTime = utc + (3600000 * offset); //
    var date = new Date(localSecondTime);
    return date;
  },

  /**
   * 
   * @param status
   * @param approval
   * @returns {String}
   */
  statusChange(status, approval) {
    var result = '';
    //
    switch (status) {
      case 0:
        result = ''
        break;
      case 1:
        result = ''
        break;
      case 2:
        result = ''
        break;
      case 3:

        if (Number(approval) === 0) {

          result = '';

        } else if (Number(approval) === 1) {

          result = '';

        } else if (Number(approval) === 2) {

          result = '';

        } else {

          result = '';
        }
        break;
      case 4:
        result = '';
        break;
      case 5:
        result = '';
        break;

    }
    return result;
  },
  /*
  * */
  inputHeight(n,ele){
    let clientHeight = n.length/2*26;
    // 
    if(clientHeight < 135){
      ele.$el.getElementsByClassName('el-select__tags')[0].style.height= 'auto';
    }else{
      ele.$el.getElementsByClassName('el-select__tags')[0].style.height='135px';
      ele.$el.getElementsByClassName('el-select__tags')[0].style.overflow='auto'
    }
  },
  /*
   * 
   * */

  exportSearchList(dowLoadFileName, result) {

    const blob = new Blob([result]);
    const fileName = dowLoadFileName + '.xls';
    // 
    var brower = '';
    if (navigator.userAgent.indexOf('Edge') > -1) {
      brower = 'Edge'
    };
    if ('download' in document.createElement('a')) { // IE
      if (brower == 'Edge') {
        navigator.msSaveBlob(blob, fileName);
        return;
      }
      const elink = document.createElement('a');
      elink.download = fileName;
      elink.style.display = 'none';
      elink.href = URL.createObjectURL(blob);
      document.body.appendChild(elink);
      elink.click();
      URL.revokeObjectURL(elink.href); // URL 
      document.body.removeChild(elink)
    } else { // IE10+
      navigator.msSaveBlob(blob, fileName);
    }
  },
  //cookie
  getCookie(name) {
    var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
    if (arr = document.cookie.match(reg)) {
      return (arr[2]);
    } else {
      return null;
    }
  },
  //cookie
  getCookieAll(){
    var aCookie = document.cookie.split(";");
    var cks = {};
    for (var i = 0; i < aCookie.length; i++) {
        var aCrumb = aCookie[i].split("=");
        if(aCrumb[0].toString().trim()=='order_list'){
            continue;
        }
        cks[aCrumb[0].toString().trim()] = aCrumb[1].toString().trim();
    }
    return  cks;
  },
  //cookie
  setCookie(name, cvalue) {
    document.cookie = name + "=" + cvalue + "; ";
  },
  //cookie
  delCookie(name) {
    var cval = this.getCookie(name);
    if (cval != null) {
      document.cookie = name + "=0;expires=" + new Date(0).toUTCString();
    }
  },
  //cookie
  clearCookie(){
    var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
    if (keys) {
        for (var i =  keys.length; i--;){
            document.cookie= keys[i]+'=0;expires=' + new Date(0).toUTCString();
        }
    }
  },
  // localStorage
  setLocalStorage(key,value){
    localStorage.setItem(key, value);
  },
   // localStorage
  getLocalStorage(key){
     return localStorage.getItem(key);
  },
   // localStorage
  removeLocalStorage(key){
    localStorage.removeItem(key);
  },
   // localStorage
  clearLocalStorage(){
    localStorage.clear();
  },
  //
  showDailog_mask(){
    let maskDiv = document.getElementById('dailog_mask');
    maskDiv.style.display = 'inline'; //.hidden = false;
  },
  //
  hiddenDailog_mask(){
    let maskDiv = document.getElementById('dailog_mask');
    maskDiv.style.display = 'none'; //.hidden = true;
  },
  // IE
  browserIsIE(){
    if (!!window.ActiveXObject || "ActiveXObject" in window){
      return true; //IE
    }else{
      return false;  //IE
    }
  },
 /**
  * list to tree
  * @param {Array} data [ ]
  * @param {String} id  [ key]
  * @param {String} pid [ key]
  * @param {String} children [ key]
  * @returns
  */
 toTree(data,id,pid,children) {
    if (data == null || data.length == 0) return [];
    data.forEach(function(item) {
      delete item[children];
    });
    var map = {};
    data.forEach(function(item) {
      map[item[id]] = item;
    });

    var val = [];
    data.forEach(function(item) {
      var parent = map[item[pid]];
      if (parent) {
        (parent[children] || (parent[children] = [])).push(item);
      } else {
        val.push(item);
      }
    });
    return val;
  },
  // 
  burialPoint(event) {
    let userInfo = JSON.parse(localStorage.getItem("userInfo"));
    let userID = localStorage.getItem('loginname');
    if (!userInfo && !userID) {
      setTimeout( () => {
        this.burialPoint(event)
      }, 1000)
      return
    }
    window.zhuge.identify(userID, {
      // // name: userInfo.displayName,
      // "": userInfo.telephoneNumber,
      // "": userInfo.mail,
      // "": userInfo.heads,
      // "": userInfo.company,
      // "": userInfo.department,
      // "": userInfo.title
    }, ()=> {
      window.zhuge.track('BPM-'+event);
    });

  },

  getUrlParam(param, href) {
      let reg = new RegExp("[?&]" + param + "=([^&]*)", "i"); // i
      href = href || window.location.href;
      let result = href.match(reg); // result
      if (result != null) {
        return decodeURIComponent(result[1]);
      } else {
        return null;
      }
    }
}



