Vue.prototype.toMony = function(source, length = 3) {
    if(!source) {return ''}
    source = String(source).split(".");
    source[0] = source[0].replace(new RegExp('(\\d)(?=(\\d{'+length+'})+$)','ig'),"$1,");
    if(source.join(".").indexOf('E') > -1 || source.join(".").indexOf('e') > -1) {
      return new Number(source.join("."));
    } else {
      return source.join(".");
    }

}
