instance.interceptors.response.use((response)  => {
  if(response.data){
    if(response.headers['content-type']!=='application/vnd.ms-excel'){
      if(response.data.hasOwnProperty('responseCode') && response.data.responseCode!='100'){
        throw new Error(`${response.data.messageList[0].message}(ID${response.data.logId})`);
      }
    };
    return response;
  }
}, (err) => {
  if (err && err.response) {
    switch (err.response.status) {
      case 400: err.message = '(400)' ; break;
      case 401:
        err.message = '(401)';
        // let url = window.location.href;
        // if(url.indexOf('bpm.vanke.com') !== -1 && url.indexOf('portal') === -1 && url.indexOf('localhost') === -1) {
        //   window.location.href = 'http://bpm.vanke.com';
        // }
        break;
      case 403: err.message = '(403)'; break;
      case 404: err.message = '(404)'; break;
      case 408: err.message = '(408)'; break;
      case 500: err.message = '(500)'; break;
      case 501: err.message = '(501)'; break;
      case 502: err.message = '(502)'; break;
      case 503: err.message = '(503)'; break;
      case 504: err.message = '(504)'; break;
      case 505: err.message = 'HTTP(505)'; break;
      default: err.message = `(${err.response.status})!`;
    }
  }else{
    err.message = '!'
  }
  return Promise.reject(err)
});

Vue.prototype.$axios = instance;

